import React from 'react';
import {Col, Button, Form, FormGroup, Input, Label, InputGroup, InputGroupAddon, CustomInput, Card, CardBody, CardFooter } from 'reactstrap'

export default class Calculrater extends React.Component{
    constructor(props){
        super(props)       
        this.state ={
            age: '',
            height: '',
            weight: '',
            gender: '',
            activity: '1.2',
            goal: '',
        }
    }

    componentDidUpdate(prevProps){
        if(this.props!==prevProps){
            this.setState(this.props)
        }
    }

    handleAgeChange = (event) => {
        this.props.onValueChange()
        this.setState({age: event.target.value})
    }
    handleWeightChange = (event) => {
        this.props.onValueChange()
        this.setState({weight: event.target.value})
    }
    handleHeightChange = (event) => {
        this.props.onValueChange()
        this.setState({height: event.target.value})
    }
    handleGenderChange = (event) => {
        this.props.onValueChange()
        this.setState({gender: event.currentTarget.value})
    }
    handleActivityChange = (event) => {
        this.props.onValueChange()
        this.setState({activity: event.target.value})
    }
    handleGoalChange = (event) => {
        this.props.onValueChange()
        this.setState({goal: event.currentTarget.value})
    }

    calculrate = (e) =>{
        e.preventDefault();
        this.props.onCreate(this.state)
    }
    
    render(){
        return(
                <Card className="text-muted text-center">
                <Form method="post" onSubmit={this.calculrate}>
                    <CardBody>
                        <FormGroup row> 
                            <Label for="age" md={3} xs={4}></Label>
                            <Col md={9} xs={8}>
                                <InputGroup>
                                <InputGroupAddon addonType="prepend"></InputGroupAddon>
                                    <Input type="text" name="age" id="age" value={this.state.age} placeholder=" " onChange={this.handleAgeChange}></Input>
                                    <InputGroupAddon addonType="append"></InputGroupAddon>
                                </InputGroup>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label for="weight"  md={3} xs={4}></Label>
                            <Col md={9} xs={8}>
                                <InputGroup>
                                <Input type="text" name="weight" id="weight" value={this.state.weight} placeholder="kg" onChange={this.handleWeightChange}></Input>
                                <InputGroupAddon addonType="append">kg</InputGroupAddon>
                                </InputGroup>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label for="height" md={3} xs={4}></Label>
                            <Col md={9} xs={8}>
                                <InputGroup>
                                <Input type="text" name="height" id="height" value={this.state.height} placeholder="cm" onChange={this.handleHeightChange}></Input>
                                <InputGroupAddon addonType="append">cm</InputGroupAddon>
                                </InputGroup>
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                            <Label md={3} xs={4}></Label>
                            <Col  md={9} xs={8}>
                                <CustomInput type="radio" id="male" name="gender" value="male" onChange={this.handleGenderChange} label="" inline/>
                                <CustomInput type="radio" id="female" name="gender" value="female" onChange={this.handleGenderChange} label="" inline />
                            </Col>
                        </FormGroup>
                        <FormGroup row>
                        <Label for="activity" md={3} xs={4}></Label>
                        <Col md={9} xs={8}>
                            <CustomInput type="select" name="activity" id="activity" value={this.state.activity} onChange={this.handleActivityChange}>
                                <option value="1.2">,   </option>
                                <option value="1.375"> 1-3 </option>
                                <option value="1.55"> 3-5 </option>
                                <option value="1.755"> 6-7 </option>
                                <option value="1.9"></option>
                            </CustomInput>
                        </Col>
                        </FormGroup>
                        <FormGroup row>
                        <Label md={3} xs={4}></Label>
                        <Col md={9} xs={8}>
                            <CustomInput type="radio" id="" name="goal" value="" onChange={this.handleGoalChange} label="" inline/>
                            <CustomInput type="radio" id="" name="goal" value="" onChange={this.handleGoalChange} label="" inline/>
                            <CustomInput type="radio" id="" name="goal" value="" onChange={this.handleGoalChange} label="" inline /> 
                        </Col>
                        </FormGroup>
                    </CardBody>
                    <CardFooter className="text-center">
                        <Button type="submit" onClick={this.calculrate}>Calculrate</Button>
                    </CardFooter>
                </Form>
                </Card>
            )
        }
    }